import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import EventAvailableIcon from '@material-ui/icons/EventAvailable'
import Paper from '@material-ui/core/Paper';
import EmailIcon from '@material-ui/icons/Email';
import Layout from 'layout/Layout';
import Metadata from 'components/Metadata';
import { ContactPageMetadata } from 'configs/PageMetadata';
import { profileLinks } from 'configs/SiteConfig';


const useStyles = makeStyles(() => ({
  root: {
    margin: 20,
    padding: 20
  },
  sectionTitle: {
    paddingBottom: 10,
    fontSize: '1.20rem'
  }
}));

export default function ContactPage() {
  const classes = useStyles();
  return (
    <Layout>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Contact
      </Typography>
        <Typography paragraph>
          You can connect with me for scheduling a guest lecture at your institution. If you are a technology enthusiast too, please feel free to network with me on LinkedIn. You can also get technical consulting from me on Digital Transformation for any good social cause. Please feel free to send me an email.
        </Typography>
        <Typography paragraph>
        <Button
          variant="contained"
          color="primary"
          size="small"
          className={classes.button}
          startIcon={<EmailIcon />}
          href={profileLinks.mail}
          target="_blank"
        > Send Email
      </Button>
        </Typography>
      </Paper>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Schedule an E-Meeting
      </Typography>
        <Typography paragraph>
          You can schedule an online meeting with me. A Google Meet link will be auto-generated and will be shared with you through an email invite. Please don't forget to update the agenda in the meeting description. Click below button to Schedule.
        </Typography>
        <Typography paragraph>
        <Button
          variant="contained"
          color="primary"
          size="small"
          className={classes.button}
          startIcon={<EventAvailableIcon />}
          href={profileLinks.appointmentSchedule}
          target="_blank"
        > Schedule E-Meeting
      </Button>
        </Typography>
      </Paper>
      <Metadata
        Title={ContactPageMetadata.Title}
        Description={ContactPageMetadata.Description}
      />
    </Layout>
  );
}